/*
*   Name: Bizagi Database Plugin
*   Author:Bizagi Mobile Team
*   Comments:
*   -   This script creates a plugin to handler all db functionalities
*   -   Pre-requisite pouchDB
*/

$.Class.extend("bizagi.workportal.services.fetchWorker", {}, {
    init: function(params) {
        var self = this;
        self.services = params;
        self.db = params.database;
        self.maxOfflineCases = 100;
        self.secondsBeforeAllowFetchAgain = 2;
        self.tasksSyncDelda = 8;
        self.startSyncDelta = 8;
        var queryString = bizagi.readQueryString();
        self.disabledb = (queryString && queryString["disabledb"]) ? eval(queryString["disabledb"]) : false;

        self.globalId = (typeof BIZAGI_USER != "undefined")
            ? ("" + BIZAGI_USER + "-" + BIZAGI_PROXY_PREFIX + "-" + BIZAGI_DOMAIN).toLowerCase()
            : "admon" + "-" + "http://localhost/bizagiR100x" + "-" + "domain";

        self.fetchInProgress = false;
        
    },

    triggerOfflineSyncEvent: function(params){
        if ( bizagi.util.isNativePluginSupported() && bizagi.util.isiOSSmartphoneDevice()) {
            bizagiapp.synchronizeDownloadOfflineState(params);
        }else{
            bizagi.webpart.publish("offline.sync", params);
        }
    },

    fetch: function(argument){
        var self = this;
			   
        var timeBeforeFetch = self.secondsBeforeAllowFetchAgain * 1000;
        if( self.lastFetch){
            if( self.lastFetch + timeBeforeFetch < Date.now() && !self.fetchInProgress){
                executeFetch();
			}
        }
        else if (!self.fetchInProgress){
            executeFetch();
        }        

        function executeFetch(){
            self.triggerOfflineSyncEvent({type:"download-started"});
            self.fetchInProgress = true;
            self.fetchInProgressPromise = new $.Deferred();

            var getOfflineTasksProm = self.services.getOfflineTasks();
            var getOfflineProcessesProm = self.services.getOfflineProcessesHierarchy();            
            var formsToDownloadCountPromise = new $.Deferred();

            var downloadStartFormsPromise;
            var downloadTaskFormsPromise;

            var offlineWorkAvailable = 0;

            $.when(getOfflineProcessesProm, getOfflineTasksProm)
            .then( function(offlineProcessesHierarchy, offlineTasksResponse){
                
                offlineProcessesHierarchy = Array.isArray(offlineProcessesHierarchy) && offlineProcessesHierarchy.length > 0 ?
                    offlineProcessesHierarchy[0]: offlineProcessesHierarchy;
                var offlineProcessesList = self.getProcessesListFromHierarchy(offlineProcessesHierarchy);
                
                
                offlineTasksResponse = Array.isArray(offlineTasksResponse) && offlineTasksResponse.length > 0 ? 
                    offlineTasksResponse[0] : offlineTasksResponse;
                var serializedOfflineActivities = self.serializeActivities(offlineTasksResponse);
                offlineWorkAvailable = offlineProcessesList.length + Object.keys(serializedOfflineActivities).length;

                $.when(self.getTasksToDownload(serializedOfflineActivities), self.getStartFormsToDownload(offlineProcessesList))
                .then( function(tasksToDownloadResponse, startFormsToDownload){
                    downloadTaskFormsPromise = self.processTasksToFetchForms(serializedOfflineActivities, tasksToDownloadResponse.items);
                    downloadStartFormsPromise = self.fetchStartForms(offlineProcessesHierarchy, startFormsToDownload);
                    formsToDownloadCountPromise.resolve(startFormsToDownload.length + tasksToDownloadResponse.count);
                })
                .fail( function(getTasksToDownloadErr, getStartFormsToDownloadErr){
                    formsToDownloadCountPromise.reject({tasks: getTasksToDownloadErr, start: getStartFormsToDownloadErr});
                });
            })
            .fail( function( startFormsSyncNeededError, offlineTasksError){
                console.log("Forms download fails", startFormsSyncNeededError, offlineTasksError);
                formsToDownloadCountPromise.reject()
            })

            $.when(formsToDownloadCountPromise)
            .then(function(count){
                if(count > 0) {
                    self.triggerOfflineSyncEvent({type:"update-total-downloads", argument: count});
                }
                else {
                    if(offlineWorkAvailable > 0){
                        self.triggerOfflineSyncEvent({type:"up-to-date", argument: offlineWorkAvailable});
                    }
                }
                return $.when.apply($, [downloadStartFormsPromise, downloadTaskFormsPromise]);
            })
            .then(function(){
                self.lastFetch = Date.now();
                self.fetchInProgressPromise.resolve(true);
                self.fetchInProgress = false;
                self.triggerOfflineSyncEvent({type:"download-finished"});
            })
            .fail( function(){
                //Wait for all downloads ends, because can fail only downloading start forms but not tasks or vice
                $.when(downloadStartFormsPromise)
                .always( function(startResult){
                    $.when(downloadTaskFormsPromise)
                    .always( function(tasksResult){
                        self.fetchInProgressPromise.reject("Error executing fetch v2");
                        self.fetchInProgress = false;
                        self.triggerOfflineSyncEvent({type:"download-error"});
                    });
                })
            })

            $.when(self.services.getRecentOfflineProcesses())
            .then(function(recentProcceses){
                self.saveRecentProcesses(recentProcceses);
            })
        }
			   
        return self.fetchInProgressPromise.promise();
    },

    fetchStartForms: function(offlineProcessTree, processesList){
        var def = new $.Deferred();
        var self = this;
        var offlineProcessTree;
        $.when(self.saveProcessesHierarchy(offlineProcessTree))
        .then( function(){
            return self.downloadStartForms(processesList)
        })
        .then( function(){
            console.log("Sv2: Offline start forms downloaded succesfully");
            def.resolve();
        })
        .fail( function(err){
            console.log("Sv2: Problem downloading any offline start form", err);
            def.reject();
        });
        return def.promise();
    },

    getProcessesListFromHierarchy: function(offlineProcessTree){
        var processes = [];

        for(var i=0; i< offlineProcessTree.length; i++ ){
            var currentApplication = offlineProcessTree[i];
            var categories = currentApplication.categories;
            for( var j = 0; j < categories.length; j++){
                getProcessesFromCategory(categories[j]);
            }
        }

        function getProcessesFromCategory(category){
            if(category.isProcess === "true" && category.hasOfflineForm){
                processes.push(category.idCategory);                
            }
            else if (category.subCategories){
                for( var k = 0; k < category.subCategories.length; k++){
                    var subCategory = category.subCategories[k];
                    getProcessesFromCategory(subCategory);
                }
            }
        }
        return processes;
    },

    getStartFormsToDownload: function(processes) {
        var self = this;
        var def = new $.Deferred();
        var checksQueue = new bizagiQueue();
        var processesToDownload = [];
        processes.forEach( function(idProcess){
            var checkProm = self.checkIfStartFormNeedsSync(idProcess);
            $.when(checkProm)
            .then( function(isSyncNeeded){
                if(isSyncNeeded){
                    processesToDownload.push(idProcess);
                }
            });
            checksQueue.add(checkProm);
        });

        $.when(checksQueue.execute())
        .always( function(){
            def.resolve(processesToDownload);
        })
        return def.promise();
    },

    checkIfStartFormNeedsSync: function( idProcess ){
        var def = new $.Deferred();
        var self = this;
        $.when( self.services.database.getStartForm({idProcess: idProcess}))
        .then( function(localForm){
            var isSyncNeeded = false;
            var lastSync = localForm.lastSynchronization;        
            if(lastSync){
                var currentTime = Date.now();   
                isSyncNeeded = lastSync + ( self.startSyncDelta*60*60*1000 ) < currentTime;  
            }
            def.resolve(isSyncNeeded);
        })
        .fail( function(){
            def.resolve(true);
        })
        return def.promise();
    },

    downloadStartForms: function(processes){
        var self = this;
        var def = new $.Deferred();
        var downloadFormsQueue = new bizagiQueue();

        var formsSuccessCount = 0;
        var formsFailedCount = 0;

        for(var i=0; i< processes.length; i++ ){
            var downloadProm = self.services.downloadOfflineStartForm({
                idProcess: processes[i]
            });
            $.when(downloadProm)
            .then( function(){
                self.triggerOfflineSyncEvent( {type:"form-finished", argument: "success"});
                formsSuccessCount++;
                checkIfDownloadStartFormsEnds();
            })
            .fail( function(){
                self.triggerOfflineSyncEvent({type:"form-finished", argument: "error"});
                formsFailedCount++;
                checkIfDownloadStartFormsEnds();
            })
            downloadFormsQueue.add(downloadProm);
        }
        $.when(downloadFormsQueue.execute())
        .then(function(){
            def.resolve();
        })
        .fail(function(){
            checkIfDownloadStartFormsEnds();
        });

        function checkIfDownloadStartFormsEnds(){
            if(formsFailedCount + formsSuccessCount == processes.length){
                if(formsFailedCount > 0){
                    def.reject();
                } 
                else{
                    def.resolve();
                }
            }
        }

        return def.promise();
    },

    serializeActivities: function(offlineTasksResponse){
        var self = this;
        var elementsMap = {};

        var row, i = -1;    
        var actualDate = new Date();
        var statusFunction = offlineTasksResponse.advanceSearch ? self.services.defineActivityStateByTaskState :self.services.defineActivityStateByDate;

        while ((row = offlineTasksResponse.rows[++i])) {
            var tempElement = {}, el = ["", "", "", "", "", "", [], "", "", "", "","",""];
            var field, j = -1;
            while ((field = offlineTasksResponse.headers[++j])) {
                tempElement[field.fieldName.toLowerCase()] = {
                    "displayName": field.displayName,
                    "value": row[j]
                };
                self.services.serializeActivity(el, field.fieldName.toLowerCase(), tempElement[field.fieldName.toLowerCase()]);
            }
            
            var tmpElement = el;
            var status = {};

            status = statusFunction(tmpElement[3], tmpElement.taskstate);

            // Time to atRiskTimeDelta
            if (tmpElement.length >= 10 && parseInt(tmpElement[11], 10) > 0) {
                var tmpEndDate = new Date(tmpElement[3]);

                if (actualDate.getTime() > tmpEndDate.getTime()) {
                    status.state = "red";
                } else {
                    if (actualDate.getTime() > (tmpEndDate.getTime() - (parseInt(tmpElement[11], 10) * 60000))) {
                        status.state = "yellow";
                    } else {
                        status.state = "green";
                    }
                }
            }

            //Tasks are offline
            tmpElement[7] = true;
            tmpElement = $.extend({}, tmpElement, status, {
                "endDate": tmpElement[3],
                "idworkitem": tmpElement[1],
                "idwfclass": !bizagi.util.validateFeatureSupport("advancedSearch") ? tmpElement[7] : tmpElement[10]
            })

            var idCase = tmpElement[0];
            var idWorkitem = tmpElement[1];
            var internalId = idCase + "-" + idWorkitem;

            elementsMap[internalId] = tmpElement;     
        }

        return elementsMap;
    },

    getTasksToDownload: function( serverActivities ){
        var self = this;
        var def = new $.Deferred();
        $.when(self.services.database.getLocalActivities())
        .then( function(localCases) {
            var itemsToDownload = {};
            var itemsCount = 0;
            var timeBeforeNextHardRedownload = 1000 * 60 * 60 * self.tasksSyncDelda;

            Object.keys(serverActivities).forEach( function(servTaskId) {
                var servTask = serverActivities[servTaskId];
                var matchingLocalTask = localCases[servTaskId];
                if( !matchingLocalTask || matchingLocalTask.dataChangeset === undefined 
                    || servTask.dataChangeset !== matchingLocalTask.dataChangeset 
                    || !matchingLocalTask.lastFullSync || (matchingLocalTask.lastFullSync + timeBeforeNextHardRedownload < Date.now())) {
                    itemsToDownload[servTaskId] = {
                        idCase: servTask[0],
                        idWorkitem: servTask[1], 
                        syncDelda: self.tasksSyncDelda,            
                        dataChangeset: servTask.dataChangeset
                    };
                    itemsCount++;
                }
            });
            def.resolve({
                items: itemsToDownload,
                count: itemsCount
            });
        })
        .fail( function(){
            def.reject();
        });
        return def.promise();
    },

    processTasksToFetchForms: function(serverTasks, tasksToDownload) {        
        var self = this;
        var def = new $.Deferred();
        var queueReadyToProcess = new $.Deferred();
        var processOfflineFormsQueue = new bizagiQueue();

        var tasksSuccessfulCount = 0;
        var tasksFailedCount = 0;
        var tasksToDownloadCount = 0;

        //Si el task fue trabajado y tiene error de sincronización no se actualiza.
        $.when(self.db.getOutboxTaskCasesFilteredBySyncStatus("any", false))
        .then( function(result){
            for(var i=0; i <result.length; i++){
                delete tasksToDownload[result[i].id];
                delete serverTasks[result[i].id];
            }
            return true;
        })
        //Buscar las actividades que están en el inbox offline
        .then( function(){
            var localActivitiesProm = new $.Deferred();
            self.db.dbActivities.allDocs({include_docs:false}, function(err, response){
                if(err)
                    localActivitiesProm.reject();
                else
                    localActivitiesProm.resolve(response.rows);
            });
            return localActivitiesProm.promise();
        })
        //Si una actividad ya no está disponible (está local pero no en la lista del server) se debe eliminar la actividad, la forma y el caso
        .then( function(localActivities) {
            for(var i = 0; i < localActivities.length; i++){
                localActivityInternalId = localActivities[i].id;
                var isActivityAvailable = serverTasks[localActivityInternalId] ? true : false;
                if(!isActivityAvailable){
                    processOfflineFormsQueue.add(self.deleteLocalDataIfActivityWasNotWorked(localActivityInternalId));
                }
            }
            queueReadyToProcess.resolve();
        })
        .fail( function(error){
            console.log("Diff algorithm error", error);
            queueReadyToProcess.resolve();
        });

        $.when(queueReadyToProcess)
        .then(function(){
            return self.services.database.saveOfflineTasks(serverTasks);  
        })
        .then( function (saveResponse) {
            Object.keys(tasksToDownload).forEach( function(key){
                var internalId = key;
                var downloadTaskParams = tasksToDownload[internalId];
                tasksToDownloadCount++;
                var processFormProm = self.services.downloadOfflineTaskForm(downloadTaskParams);
                $.when(processFormProm)
                .then( function(){
                    return self.updateActivityDatachangesetAndSyncTimestamp( internalId, downloadTaskParams.dataChangeset )
                })
                .then( function(){
                    self.triggerOfflineSyncEvent({type:"form-finished", argument: "success"});
                    tasksSuccessfulCount++;
                    checkIfDownloadTaskFormsEnds();
                })
                .fail( function(){
                    self.triggerOfflineSyncEvent({type:"form-finished", argument: "error"});
                    tasksFailedCount++;
                    checkIfDownloadTaskFormsEnds();
                })
                processOfflineFormsQueue.add(processFormProm);
            });
            return processOfflineFormsQueue.execute();
        })
        .then(function(result){
            console.log("Download of tasks forms finished SUCCESSFULL (Queue)");
            def.resolve(result);
        })
        .fail(function(err){
            checkIfDownloadTaskFormsEnds();
            if(tasksToDownloadCount == 0){
                def.reject(err);
                console.log("Error in tasks download's differential algorithm:", err);
            }
        });
        

        function checkIfDownloadTaskFormsEnds(){
            if(tasksFailedCount + tasksSuccessfulCount == tasksToDownloadCount){
                if(tasksFailedCount > 0){
                    console.log("Download of tasks forms finished with ERRORS");
                    def.reject();
                }
                else {
                    console.log("Download of tasks forms finished SUCCESSFULL (Download count)");
                    def.resolve();
                }
            }
        }

        return def.promise();
    },

    deleteLocalDataIfActivityWasNotWorked: function(internalId){
        var self = this;
        var def = new $.Deferred();
        $.when(self.db.wasActivityWorkedOffline(internalId))
        .then( function(wasWorked){
            if(!wasWorked){
                var deleteActivity = bizagi.util.db.deleteDocument(self.db.dbActivities, internalId);
                var deleteActivityForm = bizagi.util.db.deleteDocument(self.db.dbActivityForms, internalId);
            }
            def.resolve();
        })
        return def.promise();
    },

    getHierarchyObjects: function(obj, key, val) {
        var self = this;
        var objects = [];

        for (var i in obj) {
            if (!obj.hasOwnProperty(i)) continue;
            if (typeof obj[i] == "object") {
                objects = objects.concat(self.getHierarchyObjects(obj[i], key, val));
            } else if (i == key && obj[i] == val || i == key && val == '') {
                objects.push(obj);
            } else if (obj[i] == val && key == '') {
                // Only add if the object is not already in the array
                if (objects.lastIndexOf(obj) == -1) {
                    objects.push(obj);
                }
            }
        }

        return objects;
    },


    /**
     * Save in the db the user permitions to create cases
     * @param {} hierarchy 
     * @returns {} 
     */
    saveProcessesHierarchy: function(hierarchy) {
        var self = this;
        var db = self.db.dbProcessesHierarchy;
        var docHierarchy = {
            _id: self.globalId,
            doc: hierarchy,
            lastSynchronization: Date.now()
        };
        return bizagi.util.db.saveDocument( db, docHierarchy);
    },

    /**
     * Save in the db the user recent processes to create cases
     * @param {} processes 
     * @returns {} 
     */
    saveRecentProcesses: function(processes) {
        var self = this;
        var db = self.db.dbRecentProcesses;
        processes._id = self.globalId;
        return bizagi.util.db.saveDocument( db, processes);
    },


    /**
     * 
     */
    updateActivityDatachangesetAndSyncTimestamp: function(caseId, dataChangeset){
        var self = this;
        var def = new $.Deferred();
        var db = self.db.dbActivities;
        $.when(bizagi.util.db.getDocument(db, caseId))
        .then( function(activity){
            activity.dataChangeset = dataChangeset;
            activity.lastFullSync = Date.now();
            return bizagi.util.db.saveDocument(db, activity);
        })
        .then( function(saveResult){
            def.resolve();
        })
        .fail( function(error){
            def.reject(error);
        });
        return def.promise();
    }
});
