    downloadOfflineTaskForm: function(params){        
        var def = new $.Deferred();
        var self = this;
        params.deleteNextButton = false;
        
        $.when(self.getServerTaskForm(params))
        .then( function(form) {
            form.form.properties.isTaskForm = true;
            if (form.form.buttons === undefined || (Array.isArray(form.form.buttons) && form.form.buttons.length == 0 ) )
            {
                var offlineDefaultButtons = [
                    {
                        button: {
                            properties: {
                                actions: [
                                    "submitData",
                                    "refresh"
                                ],
                                caption: "render-form-button-save",
                                id: "ec0a5a02-0f81-44a7-8d64-ac090a01d61b"
                            }
                        },
                    },
                    {
                        button: {
                            properties: {
                                actions: [
                                    "validate",
                                    "submitData",
                                    "next",
                                    "routing"
                                ],
                                caption: "render-form-button-next",
                                id: "ba81c7af-0d83-4139-879d-b040f0f81bd7"
                            }
                        }
                    }
                ];
                form.form.buttons = offlineDefaultButtons;
            }
            return self.database.saveTaskForm(params, form);
        })
        .then( function(){ 
            def.resolve() 
        })
        .fail(function(){ 
            def.reject() 
        });
        return def.promise();
    },

    getServerTaskForm: function(params){
        var self = this;
        var def = new $.Deferred();
        var resolvedForm = undefined;
        params.downloadOfflineForm = true;

        $.when(self.renderDataService.getFormData(params))
        .done( function(form){
            if(form.errorType){
                def.reject(form);
            }
            form.dataChangeset = params.dataChangeset;
            if(params.deleteNextButton){
                form = self.database.deleteNextButton(form);
            }

            var data = {};
            var downloadPromises = [];
            var uploads = self.database.filterXpathsByType(form, "upload");
            uploads.forEach(function(upload){
                self.downloadFilesInUploadControl(params, form, upload, downloadPromises);
                self.convertUploadOnlineValueInOfflineValue(upload, data);
            });

            var downloadImagesPromise = self.downloadFormImages(form, params);
            downloadPromises.push(downloadImagesPromise);
            
            var mergedFormPromise = new $.Deferred();
            downloadPromises.push(mergedFormPromise);

            var formPromise = new $.Deferred();
            $.when(self.database.mergeDataDocument(data, null, formPromise))
            .done( function(complexData){
                var object = {data: data};
                object.data = complexData;
                object.isOpen = true;
                self.database.mergeForm(object, formPromise).then(function(mergedForm){
                    mergedFormPromise.resolve(mergedForm);
                    resolvedForm = mergedForm;
                });
            })
            .fail( function(err){
                mergedFormPromise.reject(err);
            });
            formPromise.resolve(form);


            $.when.apply($, downloadPromises).then(function(){
                def.resolve(resolvedForm);
            })
            .fail( function() {
                console.log("Error downloading task form", params);
                def.reject();
            });

        })
        .fail( function(err){
            def.reject(err);
        });

        return def.promise();
    },

    downloadFormImages: function(form, params){
        var def = new $.Deferred();
        var self = this;
        var imageControls = self.database.filterXpathsByType(form, "image");
        var downloadImagesPromises = [];
        imageControls.forEach( function(currentImageControl){
            downloadImagesPromises.push( self.downloadImage(params, form.form, currentImageControl) );
        });
        $.when.apply($, downloadImagesPromises).then(function(){
            def.resolve();
        }).fail( function(error){
            console.log("Error downloading images", params, error);
            def.reject();
        })
        return def.promise();
    },
