/*
*   Name: Bizagi Rendering Services
*   Author: Bizagi Mobile Team
*   Comments:
*   -   This class will provide a facade to access to workportal REST services
*/

bizagi.render.services.service.extend("bizagi.render.services.service", {},
{
    /* 
     *   Constructor
     */
    init: function(params) {
        // Call base
        var self = this;

        self._super(params);
        self.database = params.database;
    },

    getFormData: function(params) {
        var self = this;

        params = params || {};

        if (!params.isOfflineForm) {
            return self._super(params);
        } else {

            if (bizagi.util.isNativePluginSupported() && typeof(bizagiapp.getNativeFormData) !== "undefined") {
                return self.getFormDataFromNative(params)
                    .then(function(result) {
                        return self.database.mergeForm(result[1], result[0].data);
                    });

            } else {
                if(params.offlineType === 'task'){
                    //It is not a start event
                    if(params.isEdited || !params.isOpen){
                        var def = new $.Deferred();
                        $.when(self.database.getTaskData(params)).then(function(formData){
                            
                            var formPromise = self.database.getTaskForm(params)
                            $.when(self.database.mergeDataDocument(formData.data, null, formPromise)).done(function(complexData){
                                var object = {
                                    data: complexData, 
                                    isOpen: params.isOpen
                                };
                                self.database.mergeForm(object, formPromise).then(function(mergedForm){
                                    def.resolve(mergedForm);
                                });
                            });
    
                        });
                        return def.promise();
                    }
                    else
                        return self.database.getTaskForm(params);
                }
                else {
                    return $.when(self.database.getCase(params.idCase))
                    .then(function(response) {
                        var idWfClass = $.trim(response.idWfClass.toString());
                        return self.database.mergeForm(response, self.database.getFormData(idWfClass));
                    });
                }
            }
        }
    },

    getOfflineImage: function(params, fileType){
        if(params.offlineType === "task")
            return this.database.getActivityImage(params, fileType);
        else
            return this.database.getStartDataImage(params, fileType);
    },

    getOfflineActivityDataImage: function(params, fileFormat){
        return this.database.getActivityDataImage(params, fileFormat);
    },

    saveOfflineImage: function(params, image){
        if (params.operationType === "synchronization")
            return this.database.saveActivityFormImage(params, image);
        else if (params.operationType === "edition"){
            if(params.offlineType === "task")
                return this.database.saveActivityDataImage(params, image);
            else
                return this.database.saveStartDataImage(params, image);
        }
        else 
            return null;
    },

    saveOfflineFile: function(params, file, mimeType){
        if (params.operationType === "synchronization")
            return this.database.saveActivityFormFile(params, file, mimeType);
        else if (params.operationType === "edition"){
            if(params.offlineType === "task")
                return this.database.saveActivityDataFile(params, file, mimeType);
            else
                return this.database.saveStartDataFile(params, file, mimeType);
        }
        else 
            return null;
    },

    getOfflineFile: function(params, fileType, mimeType){
        if(params.offlineType === "task")
            return this.database.getActivityFile(params, fileType, mimeType);
        else
            return this.database.getStartDataFile(params, fileType, mimeType);
    },

    getOfflineActivityDataFile: function(params, fileType, mimeType){
        return this.database.getActivityDataFile(params, fileType, mimeType);
    },

    getFormDataFromNative: function(params) {
        var def = new $.Deferred();

        bizagiapp.getNativeFormData(params, function(form) {

            bizagiapp.getCase(params.idCase, function(caseInfo) {

                var result = [JSON.parse(form), JSON.parse(caseInfo)];

                def.resolve(result);

            }, function(error) {

                def.reject(error);

            });


        }, function(error) {
            def.reject(error);
        })

        return def.promise();
    },

    submitData: function(params) {
        var self = this;

        params = params || {};

        if (!params.isOfflineForm) {
            return self._super(params);
        } else {

            var data = self.resolveData(params.data || {}, params.xpathContext);
            data[self.Class.BA_CONTEXT_PARAMETER_PREFIX + "action"] = params.action;

            if (bizagi.util.isNativePluginSupported() && typeof (bizagiapp.getNativeFormData) !== "undefined") {
                return self.saveNativeFormInfo(params, data);
            }

            if(params.offlineType === 'task'){
                return self.database.saveTaskData(params,data);                 
            }
            else{
                return self.database.saveStartData(params, data);   
            }
            
        }
    },

    saveNativeFormInfo: function(params, data) {
        var self = this;

        var def = new $.Deferred();

        self.getFormDataFromNative(params).then(function(result) {
            return self.database.nativesaveStartData(data, result[1], result[0].data)
                .done(function(doc) {
                    bizagiapp.saveFormData(params, doc);
                    def.resolve(doc);
                });
        });

        return def.promise();
    }
});
